/* Animated-Search
====================================*/
.search-bar {
	float: right;
}

.search-bar .search-label {
	background: inherit;
	border-radius: 5px;
	display: block;
	height: 29px;
	overflow: hidden;
}

.search-bar .search-label .search-button, .search-bar .search-label .search-input {
	background: transparent;
	border: 0 none;
	height: 30px;
	margin: 0;
	outline: medium none;
	padding: 0;
}
.search-bar label {
	margin: 0;
}

.search-bar .search-label .search-button i {
	font-size: 16px;
	font-weight: 500;
	color: #fff;
}
.search-bar .search-label .search-button {
	background: inherit;
	color: #696969;
	height: 30px;
	transition: all 0.6s ease 0s;
	width: 30px;
}

.search-bar .search-label .search-input {
	background: #eee;
	color: #666;
	font-weight: 400;
	width: 0;
	transition: width 0.6s ease 0s;
}
.search-exp .search-bar .search-label .search-button {
	background: #eee;
	color: #c9c9c9;
}

.search-exp .search-bar .search-label .search-input {
	width: 150px;
}

