}
.sticky-wrapper.is-sticky #menu-bar .navbar-default .navbar-nav > li > a {
	height: 50px;
	line-height: 50px;
}

/*------------Animation-CSS-------------*/
.navbar-nav > li:hover > ul {
	-moz-animation: fadeinup 0.4s ease-in;
	-webkit-animation: fadeinup .4s ease-in;
	animation: fadeInUp 0.4s ease-in;
}

.navbar-nav > li > ul.dropdown-menu li:hover ul {
	-moz-animation: fadeinup .5s ease-in;
	-webkit-animation: fadeinup .5s ease-in;
	animation: fadeInUp 0.5s ease-in;
}

/*--------Animation_Hover_Effect_CSS---------------*/
@-webkit-keyframes fadeInUp {
	0% {
		opacity: 0;
		-webkit-transform: translateY(30px);
		transform: translateY(30px);
	}

	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
		transform: translateY(0);
	}
}

@keyframes fadeInUp {
	0% {
		opacity: 0;
		-webkit-transform: translateY(30px);
		-ms-transform: translateY(30px);
		transform: translateY(30px);
	}

	100% {
		opacity: 1;
		-webkit-transform: translateY(0);
		-ms-transform: translateY(0);
		transform: translateY(0);
	}
}

.fadeInUp {
	-webkit-animation-name: fadeinup;
	animation-name: fadeInUp;
}

/*------------Hover_Effect_CSS------------*/
.navbar-default .navbar-nav > li > a {
	overflow: hidden;
	height: 22px;
	line-height: 22px;
	padding: 10px 12px;
	color: #fff;

}
.navbar-default .navbar-nav > li > a > span.data-hover {
	position: relative;
	display: inline-block;
	height: 3em;
	color: #fff;
	margin: -12px -29px;
	padding: 11px 30px;
	-webkit-transition: -webkit-transform 0.3s;
	-moz-transition: -moz-transform 0.3s;
	transition: transform 0.3s;
}

.navbar-default .navbar-nav > li > a > span.data-hover::before {
	position: absolute;
	color: #fff;
	right: 100%;
	margin: -62px 6px;
	padding: 62px 24px;
	content: attr(data-hover);
	-webkit-transform: translate3d(0, 0, 0);
	-moz-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
	transition: all 0.3s ease-in-out 0s;
	_webkit-transition: all 0.3s ease-in-out 0s;
	_moz-transition: all 0.3s ease-in-out 0s;
}
.navbar-default .navbar-nav > li:hover > a > span.data-hover,
.navbar-default .navbar-nav > li:focus > a > span.data-hover {
	-webkit-transform: translateX(100%);
	-moz-transform: translateX(100%);
