	position: relative;
	display: block;
	min-height: 100%;
	max-width: 100%;
	opacity: 0.8;
}

figure figcaption {
	padding: 2em;
	color: #fff;
	text-transform: uppercase;
	font-size: 1.25em;
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;
}
figure figcaption h5 {
	color: #fff;
}

figure figcaption::before,
figure figcaption::after {
	pointer-events: none;
}

figure figcaption {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

figure h5,
figure p {
	margin: 0;
}

figure h5 {
	word-spacing: -0.15em;
	font-weight: 600;
	font-size: 115%;
}
.large-images figure h5 {
	font-size: 155%;
}
.small-images figure h5,
.owl-item .item-description h5 {
	font-size: 100%;
}

.list_item figure p {
	letter-spacing: 1px;
	font-size: 85%;
}
.small-images figure p,
.owl-item .item-description p {
	font-size: 75%;
}
.large-images figure p {
	font-size: 95%;
}

/* Bubba Hover Effect on Portfolio Image
===========================================*/
figure.effect-bubba {
	background: #727cb6;
}

figure.effect-bubba img {
	width: 100%;
	opacity: 1;
	-webkit-transition: opacity 0.35s;
	transition: opacity 0.35s;
}

figure.effect-bubba:hover img {
	opacity: 0.4;
}

/* Medium Image Border */
figure.effect-bubba figcaption::before,
figure.effect-bubba figcaption::after {
	position: absolute;
	top: 25px;
	right: 25px;
	bottom: 25px;
	left: 25px;
	content: "";
	opacity: 0;
	-webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
	transition: opacity 0.35s, transform 0.35s;
}

/* Small Image Border */
.small-images figure.effect-bubba figcaption::before,
.small-images figure.effect-bubba figcaption::after,
.owl-item figure.effect-bubba figcaption::before,
.owl-item figure.effect-bubba figcaption::after {
	top: 15px;
	right: 15px;
	bottom: 15px;
	left: 15px;
}

/* Large Image Border */
.large-images figure.effect-bubba figcaption::before,
.large-images figure.effect-bubba figcaption::after {
	top: 30px;
	right: 30px;
	bottom: 30px;
	left: 30px;
}

figure.effect-bubba figcaption::before {
	border-top: 1px solid #fff;
	border-bottom: 1px solid #fff;
	-webkit-transform: scale(0, 1);
	transform: scale(0, 1);
}

figure.effect-bubba figcaption::after {
	border-right: 1px solid #fff;
	border-left: 1px solid #fff;
	-webkit-transform: scale(1, 0);
	transform: scale(1, 0);
}

figure.effect-bubba h5 {
	padding-top: 30%;
	opacity: 0;
	-webkit-transition: -webkit-transform 0.35s;
	transition: transform 0.35s;
	-webkit-transform: translate3d(0, -20px, 0);
	transform: translate3d(0, -20px, 0);
}

figure.effect-bubba p {
	padding: 6px 2.5em 20px;
	opacity: 0;
	-webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
	transition: opacity 0.35s, transform 0.35s;
	-webkit-transform: translate3d(0, 20px, 0);
	transform: translate3d(0, 20px, 0);
}
.large-images figure.effect-bubba p {
	padding: 13px 2.5em 15px;
}
.small-images figure.effect-bubba p {
	padding: 2px 2.5em 15px;
}

figure.effect-bubba:hover figcaption::before,
figure.effect-bubba:hover figcaption::after {
	opacity: 1;
	-webkit-transform: scale(1);
	transform: scale(1);
}

figure.effect-bubba:hover h5,
figure.effect-bubba:hover p {
	opacity: 1;
	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}

