	border: 1px solid #727cb6;
	display: block;
	background: #727cb6;
	color: #fff;
	padding: 12px 45px;
}

.panel-title > a:hover,a:focus {
	text-decoration: none;
	outline: none;
}

.panel-title .left-icon {
	border-right: 1px solid #fff;
	font-size: 24px;
	color: #fff;
	width: 40px;
	padding: 13px 0;
	text-align: center;
	display: inline-block;
	margin-right: 10px;
	position: absolute;
	left: 0;
	top: 0;
}

.panel-title a::after {
	content: "\f068";
	font-family: FontAwesome;
	float: right;
	font-size: 20px;
	margin-right: 20px;
	color: #fff;
	position: absolute;
	top: 15px;
	right: 0;
	display: inline-block;
}

.panel-title .collapsed::after {
	content: "\f067";
	color: #727cb6;
}

.panel-title .collapsed {
	background: #fff !important;
	color: #6d6d6d;
	border-color: lightgray;
}

.panel-title .collapsed .left-icon {
	color: #727cb6;
	border-color: lightgray;
}

.panel-default > .panel-heading + .panel-collapse > .panel-body {
	border-top: 0 none;
}

